<template>
  <div class="new-post">
    <el-row :gutter="20">
      <el-col :span="10" :offset="8">
        <el-icon>folder</el-icon>
        <br></br>
        <br></br>
        <div class="el-list-text-container">
          <el-input-container>
            <label>Name</label>
            <el-input v-model="post.name" placeholder="Name"></el-input>
          </el-input-container>
        </div>
        <el-avatar class="el-avatar-icon">
          <el-icon>folder</el-icon>
        </el-avatar>
          <div class="el-list-text-container">
            <el-input-container>
              <label>Content</label>
              <el-input v-model="post.content" type="textarea" rows="2" placeholder="Leave a comment here"></el-input>
            <!-- <el-textarea v-model="post.content" placeholder=""></el-textarea> -->
            </el-input-container>
          </div>
          <br></br>
        <!-- <el-button class="el-raised el-primary" @click.native="createPost">Save</el-button> -->
          <el-button class="el-raised el-primary" @click.native="createPost" type="primary" id="signin">Comment</el-button>
        </el-list>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import PostsApi from '../../api/posts.js'
import router from '../../router'

export default {
  name: 'new-post',
  data () {
    return {
      post: {
        name: '',
        content: ''
      }
    }
  },
  methods: {
    createPost () {
      PostsApi.createPost(this.post,
        function (_post) {
          router.push({ name: 'Posts.index' })
        }
      )
    }
  }
}
</script>

<style scoped>

</style>



