<template>

  <div>
  <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <el-menu-item index="1" @click.native="indexx">Home</el-menu-item>
    <el-menu-item index="2" @click.native="newPost">Post</el-menu-item>
    <el-menu-item index="3" @click.native="logout">Log Out</el-menu-item>
  </el-menu>
</div>

</template>

<script>
import router from './router'
import UsersApi from './api/users.js'

export default {
  data () {
    return {
      activeIndex: '1'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    newPost () {
      router.push({ name: 'Posts.new' })
    },
    indexx () {
      router.push({name: 'Posts.index'})
    },
    logout () {
      UsersApi.logout()
      router.push({ name: 'Users.sign_in' })
    }
  }
}
</script>

<style>
#el-menu-demo{
  position: fixed;
  top: 0;
}
</style>



