<template>
  <div class="new-post">
    <el-row :gutter="20">
      <el-col :span="10" :offset="7">
    <el-list>
      <el-list-item>
        <el-avatar class="el-avatar-icon">
          <el-icon>folder</el-icon>
        </el-avatar>
        <div class="el-list-text-container">
          <el-input-container>
            <label>Add Comment</label>
            <br></br>
            <el-input type="textarea" rows="2" placeholder="Leave a comment here" v-model="comment.content"></el-input>
            <br></br>
          </el-input-container>
        </div>
      </el-list-item>
      <el-list-item>
        <span style="flex: 1"></span>
        <el-button class="el-raised el-primary" @click.native="createComment" type="primary" id="signin">Comment</el-button>
        <br></br>
        <br></br>
        <br></br>
        <span style="flex: 1"></span>
      </el-list-item>
    </el-list>
  </el-col>
</el-row>
  </div>
</template>


<script>
import CommentsApi from '../../api/comments.js'
import router from '../../router'

export default {
  name: 'new-comment',
  data () {
    return {
      comment: {
        content: ''
      }
    }
  },
  props: {
    post: {
      type: Object,
      required: false,
      default: {}
    }
  },
  methods: {
    createComment () {
      var postId = this.post.id
      var content = this.comment.content
      this.comment.content = ''
      CommentsApi.createComment(postId, content,
        function (_comment) {
          console.log(_comment)
          router.push({ name: 'Posts.show', params: { post_id: postId }, query: { t: new Date() } })
        }
      )
    }
  }
}
</script>

<style scoped>

</style>



