<template>
  <div class="post">
    <el-list-item>
      <el-avatar>
      </el-avatar>

      <el-row :gutter="24">
        <el-col :span="10" :offset="7">
          <el-card :body-style="{ padding: '5px'}">
            <div style="padding: 14px;">
              <div class="bottom clearfix">
                <div class="el-list-text-container">
                  <div style="font-weight: bold">
                  <span>{{ post.name }}</span>
                </div>
                  <p>{{ post.content }}</p>
                </div>
              </div>
            </div>
          </el-card>
          <br></br>
        </el-col>
      </el-row>

      <el-divider class="el-inset"></el-divider>
    </el-list-item>
  </div>
</template>

<script>

export default {
  name: 'post',
  props: {
    post: {
      type: Object,
      required: false,
      default: {}
    }
  }
}
</script>

