<template>
  <el-row type="flex" class="row-bg" justify="space-around">
  <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple-light">
    <br></br>
    <br></br>
    <h1>Please Login</h1>
    <br></br>
    <el>Username</el>
    <el-input placeholder="Username" v-model="username" @keyup.enter.native="login"></el-input>
    <br></br>
    <el>Password</el>
    <el-input placeholder="Password" type="password" v-model="password"></el-input>
    <br></br>
    <el-button type="primary" id="signin" @click.native="login">Sign in</el-button>
  </div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
  </el-row>
</template>

<script>
  import UsersApi from '../api/users.js'
  import router from '../router'
  export default {
    data () {
      return {
        username: '',
        password: ''
      }
    },
    methods: {
      login () {
        console.log('methods.login')
        UsersApi.login(this.username, this.password, function (_response) {
          // NOTE: Posts.index is not yet implemented
          router.push({ name: 'Posts.index' })
        })
      }
    }
  }

</script>

<style>
  #signin{
    width: 50%;
    background-color: #7C24BF;
    border: transparent
  }
</style>



