<template>
  <div id="app">
    <div v-if="checkLoggedIn()== true">
    <iccs340-nav></iccs340-nav>
    <el-tabs></el-tabs>
    <!-- <el-tabs v-model="activeName" @tab-click="handleClick" id="tab">
      <el-tab-pane label="User" name="first"></el-tab-pane>
      <el-tab-pane label="Comment" name="second"></el-tab-pane> -->
    <!-- <iccs340-left-sidenav></iccs340-left-sidenav> -->
    </div>
    <router-view></router-view>
</div>
</template>

<script>
import State from './store'
export default {
  methods: {
    checkLoggedIn () {
      if (State.state.auth) {
        return true
      }
    }
  },
  name: 'app',
  components: {
    ElTabs: require('./tabs')
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



